import * as React from 'react'
import { PageProps } from 'gatsby'
import { service as serviceData } from '../../content/intl/ja.json'

interface ServiceProps extends React.HTMLProps<HTMLDivElement> {
}

export default class extends React.Component<ServiceProps, any> {
  public render() {
    return (
      <section id="service">
        <div className="container">
          <h1>{ serviceData.title }</h1>
          <div className="service-background">
            <div className="row row-cols-1 row-cols-md-2">
            { serviceData.items.map((d: any, i: number) => (
              <div key={i} className={`${i === 0 ? 'col-md-12' : 'col'}`}>
                <div className={`service-item service-item-${i+1}`} style={{ borderColor: d.color }}>
                  <h2 style={{ color: d.color}}>{ d.label }</h2>
                  <p>{ d.description }</p>
                </div>
              </div>
            )) }
            </div>
          </div>
        </div>
      </section>
    )
  }
}
